*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  width:100%;
  
  
  }
  
  :root {
    --color-bg: #ccccc6;
    --color-bg-variant: rgb(247, 244, 242);
    --color-primary: #80dca9 ;
    --color-primary-variant: #d8e3dd ;
    --color-white: rgb(251, 251, 250);
    --color-white-variant:rgb(241, 235, 222);
    --color-black: #3b3434;
    --color-light:rgb(77, 69, 69);
     transition:  all 400ms ease;
  
    --container-width-lg:100%;
    --container-width-md:100%;
    --container-width-sm:100%;
    
  
  
  }
  
  html{
  
    scroll-behavior: smooth;
    overflow-x: hidden;
  }
  
  ::-webkit-scrollbar{
    display:none;
  }
  
  body {
    
    /* background: var(--color-bg); */
    background:white;
    color: var(--color--black);
    line-height: 1.7;
    font-family: 'Cormorant Garamond', serif;
    /* width: 100%; */
    /* background-image: url(../src/assets/bg-texture.png); */
    overflow-x: hidden;
  
  }
  
  /*================= GENERAL STYLES =======*/
  
  
  .container {
    width: 100%;
    margin : 0 auto;
  
  }
  
  h1,
  h2,
  h3,
  h4,
  h5 {
  
  
    font-weight: 300;
  
  
  }
  
  
  h1 {
    font-size: 2.5rem;
  }
  
  section {
  
    /* margin-top: 8rem; */
    height:100vh;
    font-size:12px;
  
  }
  
  section > h2, section > h5{
    text-align: center;
    /* color:var(--color-light); */
    /* color: var(--color-primary); */
   
  }
  
  /* section > h2{
    
    color:var(--color-primary);
    
    
    margin-bottom: 3rem;
  } */
  
  .text-light {
  
    color: var(--color-light);
  }
  
  a{
    /* color: var(--color-primary); */
    transition: var(--transition);
  }
  
  a:hover {
    color : var(--color-white);
  }
  
  .btn {
    width:max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
  }
  
  .btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
  }
  
  .btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
    
  }
  
  img {
    display: block;
    width: 100%;
    object-fit: cover;
  }
  
  
  /*======== MEDIA QUERIES =======*/
  
  @media screen and (min-width:992px){
  
    .container {
      width: var (--container-width-md);
    }
    section {
      margin-top:6rem;
    }
  }
  
  @media  screen and (min-width:600px) {
  
     .container {
      width: var (--container-width-sm);
    }
  
    section {
      margin-top:2.5rem;
    }
  }
